import { Player } from "../../player/player"
import { dynamiteGravity } from "./dynamiteGravity"


export class Dynamite{
    constructor(posX,posY,directionAttack){
        this.posX=posX
        this.posY=posY
        this.size=30
        this.color="red"
        this.name="dynamite"
        this.startPosX=posX
        this.startPosY=posY
        this.directionAttack=directionAttack
        this.speed=2
        this.fallenSpeed=0.8
    }

    draw(can,arrBullet){
        dynamiteGravity()

        can.ctx.fillStyle=this.color
        can.ctx.fillRect(this.posX,this.posY,this.size,this.size)
        if(this.directionAttack==="right"){
           
            let distanceX=this.posX-this.startPosX
            if(distanceX>=50){
                this.posY+=this.speed
                this.posX+=this.fallenSpeed
                
            }
            else{
                this.posX+=this.speed
                this.posY-=this.speed
            }
        }
      
        

    }
}